	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2012/8/foss-patents-apples-billion-dollar-win-over-samsung-is-a-huge-breakthrough-but-its-not-thermonuclear/index.md absPath of file >>> MarkdownRemark","html":"<p>FOSS Patents has an insightful article about the Apple vs Samsung verdict:</p>\n<blockquote>\n<p>Samsung has issued a statement that claims this jury verdict is a loss for consumers. But things are more complex than that. There can be no reasonable doubt that Samsung and Google have engaged, and continue to engage, in “copytition” (competing through copying) rather than wholly-independent creation. Somewhere the courts have to draw the line and afford some degree of protection to innovators. I don’t always agree with Apple’s claims, and I don’t like all of Apple’s patents, but the kind of disregard for other companies’ intellectual property that Samsung and Google effectively propose is certainly not the answer.</p>\n</blockquote>","frontmatter":{"title":"FOSS Patents on Apple vs Samsung","date":"August 26, 2012","passthroughUrl":"http://www.fosspatents.com/2012/08/apples-billion-dollar-win-over-samsung.html"}}},"pathContext":{"slug":"/blog/2012/8/foss-patents-apples-billion-dollar-win-over-samsung-is-a-huge-breakthrough-but-its-not-thermonuclear/","previous":{"fields":{"slug":"/blog/V2h5LXdhaXRpbmct/"},"frontmatter":{"title":"Why waiting in line is torture","passthroughUrl":"http://www.nytimes.com/2012/08/19/opinion/sunday/why-waiting-in-line-is-torture.html?_r=1&pagewanted=all"}},"next":{"fields":{"slug":"/blog/2012/9/former-iphone-ui-designer-defends-apples-skeuomorphic-design-philosophy/"},"frontmatter":{"title":"Former iPhone UI Designer Defends Apple's Skeuomorphic Design Philosophy","passthroughUrl":"http://www.fastcodesign.com/1670796/a-former-iphone-ui-designer-defends-apples-fake-leather-design-philosophy"}}}}

